import React, { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faComment } from "@fortawesome/free-solid-svg-icons";
import { faUserAlt } from "@fortawesome/free-solid-svg-icons";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { motion } from "framer-motion";
import { useLocation } from "react-router-dom";

const Nav = () => {
  const { pathname } = useLocation();
  const [pageOffset, setPageOffset] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.scrollY > 0) setPageOffset(true);
      else setPageOffset(false);
    });
  }, []);

  return (
    <StyledNav isScroll={pageOffset}>
      <StyledH1>
        <a href="#">Dems</a>
      </StyledH1>
      <ul>
        <li>
          <a href="#">Home</a>
          <Line
            transition={{ duration: 0.75 }}
            initial={{ width: "0%" }}
            animate={{ width: pathname === "/" ? "50%" : "0%" }}
          />
        </li>
        <li>
          <a href="#">All Ads</a>
        </li>
        <li>
          <a href="#">Contact</a>
        </li>
        <li>
          <a href="#">
            <FontAwesomeIcon icon={faComment} />
          </a>
        </li>
        <li>
          <a href="#">
            <FontAwesomeIcon icon={faUserAlt} />
          </a>
        </li>
        <li>
          <button>
            <FontAwesomeIcon icon={faPlus} /> <span>Post your Ad</span>
          </button>
        </li>
      </ul>
    </StyledNav>
  );
};

const StyledNav = styled.nav`
  display: flex;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 300;
  transition: all 0.2s ease;

  ${(props) =>
    props.isScroll
      ? css`
          background: white;
          box-shadow: 5px 0 10px lightgray;
          a {
            color: black;
          }
        `
      : css`
          background: transparent;

          a {
            color: white;
          }
        `}
  a {
    text-decoration: none;
    font-size: 13px;
    font-weight: 600;
    line-height: 16px;
    font-family: "Inter", sans-serif;
  }
  ul {
    list-style: none;
    display: flex;
    align-items: center;
  }
  button {
    background: #f85c70;
    border-radius: 18px;
    border: 1px solid #f85c70;
    color: white;
    width: 155px;
    height: 50px;
    cursor: pointer;
    font-family: "Inter", sans-serif;
    span {
      margin-left: 5px;
    }
  }
  li {
    padding-left: 2.25rem;
    position: relative;
  }
`;

const StyledH1 = styled.h1`
  a {
    font-size: 36px;
    font-weight: bold;
    line-height: 45px;
    font-family: "Quicksand", sans-serif;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  }
`;

const Line = styled(motion.div)`
  height: 0.3rem;
  background: #f85c70;
  width: 0%;
  position: absolute;
  bottom: -80%;
  left: 50%;
`;

export default Nav;
